import React, { useState } from 'react';
import StarRating from './StarRating';
import './ProductInfo.css';

const ProductInfo = ({ product, quantity, setQuantity,addToCart }) => {
  const handleQuantityChange = (change) => {
    const newQuantity = quantity + change;
    if (newQuantity >= 1) {
      setQuantity(newQuantity);
    }
  };

  return (
    <div className="product-info">
      <div className="product-badge">
        <span className="dress-badge">Dress</span>
      </div>
      
      <div className="product-rating">
        <StarRating rating={product.rating} readOnly={true} />
        <span className="review-count">{product.reviewCount} Reviews</span>
      </div>

      <h1 className="product-title">{product.name}</h1>
      <div className="stock-status">
        <span className="in-stock">In Stock</span>
      </div>

      <div className="product-pricing">
        <span className="original-price">${product.originalPrice}</span>
        <span className="current-price">${product.price}</span>
      </div>

      <div className="product-description">
        <p>Piyoshop has brought to you the Hijab 3 Pieces Combo Pack PS23. It is a completely modern design and you feel comfortable to put on this hijab. Buy it at the best price.</p>
      </div>

      <div className="quantity-section">
        <div className="quantity-controls">
          <button 
            className="quantity-btn"
            onClick={() => handleQuantityChange(-1)}
            disabled={quantity <= 1}
          >
            -
          </button>
          <span className="quantity-display">{quantity.toString().padStart(2, '0')}</span>
          <button 
            className="quantity-btn"
            onClick={() => handleQuantityChange(1)}
          >
            +
          </button>
        </div>
        
        <button className="add-to-cart-btn" onClick={addToCart}>
          <span className="cart-icon" >🛒</span>
          Add to Cart
        </button>
      </div>

      <div className="product-meta">
        <div className="meta-item">
          <span className="meta-label">SKU:</span>
          <span className="meta-value">{product.sku}</span>
        </div>
        <div className="meta-item">
          <span className="meta-label">Categories:</span>
          <span className="meta-value">{product.categories.join(', ')}</span>
        </div>
        <div className="meta-item">
          <span className="meta-label">Tags:</span>
          <span className="meta-value">{product.tags.join(', ')}</span>
        </div>
      </div>

      <div className="share-section">
        <span className="share-label">Share:</span>
        <div className="share-buttons">
          <button className="share-btn facebook">f</button>
          <button className="share-btn twitter">t</button>
          <button className="share-btn linkedin">in</button>
          <button className="share-btn pinterest">p</button>
          <button className="share-btn email">@</button>
        </div>
      </div>
    </div>
  );
};

export default ProductInfo;